import { ReactNotifications } from 'react-notifications-component';
import { Route, Routes } from 'react-router-dom';
import { connect } from 'react-redux';
import { useEffect } from 'react';
import Header from './component/Header';
import NavbarContainer from './container/Navbar/NavbarContainer';
import Login from './pages/Login'
import Registration from './pages/Registration';
import Home from './pages/Home';
import LocationsContainer from './container/Locations';
import './App.css';
import { RequiredAuth } from './HOC/RequiredAuth';
import { isAuthS } from './store/auth/selectors';
import { fetchUserDataA } from './store/auth/actions';

function App({ isAuth, fetchUserData }) {

  useEffect(() => {
    if (isAuth) {
      fetchUserData()
    }
  }, [isAuth])

  return (
      <div className='app'>
        <div className="workspace-container">
            <Header />
            <div name="target" className={isAuth ? 'content' : ''}>
              <NavbarContainer isAuth={isAuth} />
              <Routes>
                <Route path="/" element={<RequiredAuth>
                  <Home />
                </RequiredAuth>} />
                <Route path="/location" element={<RequiredAuth>
                  <LocationsContainer />
                </RequiredAuth>} />
                <Route path="/login" element={<Login />} />
                <Route path="/registration" element={<Registration />} />
              </Routes>
            </div>
            <ReactNotifications />
        </div>
      </div>
  );
}

export default connect((state) => ({ 
  isAuth: isAuthS(state) 
}),
 {
  fetchUserData: () => fetchUserDataA(),
})(App)
