import {
    FETCH_VIDEO_LIST_REQUEST,
    FETCH_VIDEO_LIST_SUCCESS,
    FETCH_VIDEO_LIST_ERROR
   } from './types'

const InitialState = {
    videoList: [],
    isFetching: false,
    error: false,
}

export const videoReducer = (state = InitialState, action) => {
    switch(action.type) {
        case FETCH_VIDEO_LIST_REQUEST: 
            return { ...state, isFetching: true, error: false }
        case FETCH_VIDEO_LIST_SUCCESS:
            return { ...state, isFetching: false, error: false, videoList: action.payload }
            case FETCH_VIDEO_LIST_ERROR:
                return { ...state, isFetching: false, error: true, }
        default: 
            return state;
    }
}