import {
    LOGIN_REQUEST,
    REGISTRATION_REQUEST,
    FETCH_USER_DATA_REQUEST,
    LOGOUT
   } from './types'

export const LoginA = (data, callback) => ({ type: LOGIN_REQUEST, payload: {data: data, callback: callback} });

export const registrationA = (data, callback) => ({ type: REGISTRATION_REQUEST, payload: {data: data, callback: callback} })

export const fetchUserDataA = () => ({ type: FETCH_USER_DATA_REQUEST })

export const LogoutA = () => ({ type: LOGOUT });