import { connect } from "react-redux";
import React, { useState } from "react";
import { Store } from 'react-notifications-component';
import { useNavigate } from "react-router-dom";
import { Input, Button, Divider, Label } from "semantic-ui-react";
import { registrationA } from "../../store/auth/actions";
import { showNotification } from "../../container/Notifications";
import './style.css'

const Registration = ({
    registration,
}) => {
    const navigate = useNavigate();
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirm, setConfirm] = useState('')

    const handleChange = (e, data) => {
        e.preventDefault();
        switch (data.name) {
            case 'email':
                setEmail(data.value)
                break;
            case 'password':
                setPassword(data.value)
                break;
            case 'confirm':
                setConfirm(data.value)
                break;
            default:
                break;
        }
    }

    const onSubmit = () => {
        // let data = new FormData();
        // data.append('email', email)
        // data.append('password', password)
        if (!email) {
            return Store.addNotification({
                message: "Please enter Email",
                type: "warning",
                container: "bottom-right",
                dismiss: {
                    duration: 3000,
                }
            });
        }
        if (!password) {
            return Store.addNotification({
                message: "Please enter Password",
                type: "warning",
                container: "bottom-right",
                dismiss: {
                    duration: 3000,
                }
            });
        }
        if (!confirm) {
            return Store.addNotification({
                message: "Please enter Confirm Password",
                type: "warning",
                container: "bottom-right",
                dismiss: {
                    duration: 3000,
                }
            });
        }
        if (password && confirm && password !== confirm) {
            return Store.addNotification({
                message: "Wrong Confirm Password",
                type: "warning",
                container: "bottom-right",
                dismiss: {
                    duration: 3000,
                }
            });
        }
        return registration({email: email, password: password}, () => showNotification('ERROR', 'Something Wrong'))
    }

    const handleBackToLogin = () => {
        navigate('/login', { replace: true })
    }

    return (
        <form className="registration_container" key="email">
            <Input
                placeholder="Email"
                name="email"
                value={email}
                onChange={(e, data) => handleChange(e, data)}
            />
            <Input
                placeholder="Password"
                type="password"
                name="password"
                value={password}
                onChange={(e, data) => handleChange(e, data)}
            />
            <Input
                placeholder="Confirm Password"
                type="password"
                name="confirm"
                value={confirm}
                onChange={(e, data) => handleChange(e, data)}
            />
            <Divider />
            <div className="registration_button_container">
                <Button className="registration_button" type="button" size="medium" onClick={() => onSubmit()}>Registration</Button>
            </div>
            <Label className="registration_label" onClick={() => handleBackToLogin()}>Back to Login</Label>
        </form>
    )
}

export default connect(null, {
    registration: (data, callback) => registrationA(data, callback),
})(Registration)
