import { put, takeEvery, call } from 'redux-saga/effects'
import { 
    FETCH_VIDEO_LIST_REQUEST,
    FETCH_VIDEO_LIST_SUCCESS,
    FETCH_VIDEO_LIST_ERROR,
 } from './types'
 import { fetchYoutubeVideo } from '../../services/homeAPI'

function* fetchVideoListSaga() {
    try {
        const data = yield call(fetchYoutubeVideo);
        yield put({
            type: FETCH_VIDEO_LIST_SUCCESS,
            data
          });
    }
    catch (error) {
        yield put({
            type: FETCH_VIDEO_LIST_ERROR,
          });
    }
}

export function* VideoWatcher() {
    yield takeEvery(FETCH_VIDEO_LIST_REQUEST, fetchVideoListSaga)
}