import axios from "axios";

export function auth(data) {
    return axios.post('http://54.234.128.52:8082/auth/login', data)
}

export function registration(data) {
    return axios.post('http://54.234.128.52:8082/auth/register', data)
}

export function fetchUserDataAPI(email) {
    return axios.get(`http://54.234.128.52:8082/auth/get/${email}`)
}

