import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { Button, Header, Icon } from "semantic-ui-react";
import YouTube from "react-youtube";
import { fetchVideoListA } from "../../store/userData/actions";
import './style.css'

const Home = ({ fetchVideoList }) => {

    const [selectedVideo, setSelectedVideo] = useState('')
    const [counter, setCounter] = useState(0)

    const videoData = [
        '2g811Eo7K8U`',
        'jVFcjawQixc',
        '4ZpnCghw_HI',
    ]

    const opts = {
        height: '200',
        width: '370',
        playerVars: {
            // https://developers.google.com/youtube/player_parameters
            autoplay: 1,
        }
    }

    const videoReady = (e) => {
        e.target.pauseVideo()
    }

    const handleArrowClick = (flag) => {
        if (flag === 0 && counter === 0) {
            console.log('lap minus')
            return setCounter(videoData.length - 1)
        }
        if (flag === 1 && counter === videoData.length - 1) {
            console.log('lap plus');
            return setCounter(0)
        }
        if (flag === 0) {
            console.log('minus');
            return setCounter(counter -1)
        }
        if (flag ===1) {
            console.log('plus');
            return setCounter(counter +1)
        }
    } 

    useEffect(() => {
        setSelectedVideo(videoData[counter])
    }, [counter])

    useEffect(() => {
        fetchVideoList()
    }, [])

    return (
        <div className="home_container">
            <div className="home_content">
                <Header as="h2">Welcome Business Name</Header>
                <div>
                    This portal is released is its first beta release.
                    In this current implementation you have an ability to link all your
                    locations, hardware and software in this account.
                </div>
            </div>
            <div className="home_content">
                <div className="home_content_header">
                    <Header as="h2">Exclusive Video Content</Header>
                    <div className="home_icon_container">
                        <Icon name="angle left" size="big" link onClick={() => handleArrowClick(0)} />
                        <Icon name="angle right" size="big" link onClick={() => handleArrowClick(1)} />
                    </div>
                </div>
                <div><YouTube videoId='jVFcjawQixc' opts={opts} onReady={(e) => videoReady(e)} /></div>
                <Header as="h4">PointofNteraction.net | How to videos for small business owner</Header>
                <div>Check out YouTube CEO, Susan Wojcicki's latest letter on safer gaming policies,
                    preventing creator burnout and more</div>
                <Button color="red" size="small">SUBSCRIBE</Button>
            </div>
        </div>
    )
}

export default connect(null, {
    fetchVideoList: () => fetchVideoListA(),
})(Home)