import { Store } from "react-notifications-component";

export const showNotification = (type, message) => {
    if (type === 'INFO') {
        return Store.addNotification({
            message: message,
            type: "info",
            container: "bottom-right",
            dismiss: {
                duration: 3000,
            }
        });
    }
    if (type === 'SUCCESS') {
        return Store.addNotification({
            message: message,
            type: "success",
            container: "bottom-right",
            dismiss: {
                duration: 3000,
            }
        });
    }
    if (type === 'ERROR') {
        return Store.addNotification({
            message: message,
            type: "danger",
            container: "bottom-right",
            dismiss: {
                duration: 3000,
            }
        });
    }
    if (type === 'WARNING') {
        return Store.addNotification({
            message: message,
            type: "warning",
            container: "bottom-right",
            dismiss: {
                duration: 3000,
            }
        });
    }
}