import React, { useState } from "react";
import { Store } from 'react-notifications-component';
import { useNavigate } from "react-router-dom";
import { Input, Label, Button, Divider } from "semantic-ui-react";
import { connect } from "react-redux";
import { LoginA } from "../../store/auth/actions";
import { isFetchingS } from "../../store/auth/selectors";
import { showNotification } from "../../container/Notifications";
import './style.css'

const Login = ({
    isFetching,
    login,
}) => {
    const navigate = useNavigate();
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const handleChange = (e, data) => {
        e.preventDefault();
        switch (data.name) {
            case 'email':
                setEmail(data.value)
                break;
            case 'password':
                setPassword(data.value)
                break;
            default:
                break;
        }
    }

    const onSubmit = () => {
        if (!email) {
            return Store.addNotification({
                message: "Please enter Email",
                type: "warning",
                container: "bottom-right",
                dismiss: {
                    duration: 3000,
                }
            });
        }
        if (!password) {
            return Store.addNotification({
                message: "Please enter Password",
                type: "warning",
                container: "bottom-right",
                dismiss: {
                    duration: 3000,
                }
            });
        }
        return login({ email: email, password: password }, () => showNotification('ERROR', 'Wrong Email or Password'));
    }

    const handleClickRegistration = () => {
        navigate('/registration')
    }

    return (
        <form className="login_container" key="email">
            <Input placeholder="Email" name="email" value={email} onChange={(e, data) => handleChange(e, data)} />
            <Input placeholder="Password" type="password" name="password" value={password} onChange={(e, data) => handleChange(e, data)} />
            <Divider />
            <div className="login_button_container">
                <Button className="login_button" loading={isFetching} type="button" onClick={() => onSubmit()} size="medium">Login</Button>
            </div>
            <Label className="login_label">Forgot password?</Label>
            <Label className="login_label" onClick={() => handleClickRegistration()}>Don`t have an account? Register</Label>
        </form>
    )
}


export default connect((state) => ({ 
    isFetching: isFetchingS(state) 
  }), {
    login: (data, callback) => LoginA(data, callback),
})(Login)