import React from "react";
import { Header, Button, Input, Divider } from "semantic-ui-react";
import './style.css'

const Location = ({
    locationNumber,
    addMode,
    isNumber,
    locationsData,
    handleChangeLocationNumber,
    handleClickAddButton,
    handleClickAddNumber,
}) => {

    return (
        <div className="location_container">
            <Header as="h2">Locations</Header>
            <div className="location_content">
                {!addMode ?
                    <Button
                        size="small"
                        icon="add"
                        color="blue"
                        content="Add Locations"
                        onClick={() => handleClickAddButton()}
                    /> : (
                        <div>
                            {!isNumber ? (
                                <div className="location_number_container">
                                    <Header as="h3">Number of Locations</Header>
                                    <Divider />
                                    <div className="location_number_sub_container">
                                        <Header as="h4">How many locations are you going to add today?</Header>
                                        <div className="location_number_input_group">
                                            <Input type="number" min="1" value={locationNumber} onChange={(e, data) => handleChangeLocationNumber(e, data)} />
                                            <Button
                                                className="location_button"
                                                color="blue"
                                                content="Add"
                                                icon="plus"
                                                onClick={() => handleClickAddNumber()}
                                            />
                                        </div>
                                    </div>
                                </div>
                            ) : (
                                <div className="location_content_wrapper">
                                    <Header as="h3" content={`Create ${locationNumber} Locations`} />
                                    <div className="location_content_container">
                                        {locationsData.map(item => (
                                            <div>
                                                <Header as="h3" content={item.name} />
                                                <Divider />
                                                <div>{item.value}</div>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            )}
                        </div>
                    )}
            </div>
        </div>
    )
}

export default Location;