import React, { useEffect } from "react";
import { connect } from "react-redux";
import { Icon, Image } from "semantic-ui-react";
import { useNavigate } from "react-router-dom";
import { LogoutA } from "../../store/auth/actions";
import './style.css'

const Header = ({
    logout,
}) => {
    const navigate = useNavigate();
    const isAuthUser = localStorage.getItem('token')

    const LogOut = () => {
        logout()
        localStorage.clear()
        document.location.reload()
    }

    return (
        <div className="header_container">
            <div>
                <div className="header_blue_text">Merch<span className="header_black_text">antStack.net</span></div>
            </div>
            {isAuthUser ? (
            <div>
                
                <Icon name="bell" size="big" link />
                <Image  avatar />
                <Icon name="sign-out" size="big" color="blue" link onClick={() => LogOut()} />
            </div>) : null}
        </div>
    )
}

export default connect(null,{
    logout: LogoutA,
})(Header)