import { put, takeEvery, call } from 'redux-saga/effects'
import { auth, registration, fetchUserDataAPI } from '../../services/authAPI'
import {
    LOGIN_REQUEST,
    LOGIN_SUCCESS,
    LOGIN_ERROR,
    REGISTRATION_REQUEST,
    REGISTRATION_SUCCESS,
    REGISTRATION_ERROR,
    FETCH_USER_DATA_SUCCESS,
    FETCH_USER_DATA_REQUEST,
    FETCH_USER_DATA_ERROR
} from './types'

function* loginSaga(action) {
    try {
        const payload = yield call(auth, action.payload.data)
        yield put({
            type: LOGIN_SUCCESS,
            payload: payload,
        })
        localStorage.setItem('token', payload.data.token)
        localStorage.setItem('id', payload.data.id)
        localStorage.setItem('email', payload.data.email)
        window.location.reload()
    }
    catch (error) {
        yield call(action.payload.callback)
        yield put({
            type: LOGIN_ERROR,
        })
    }
}

function* registrationSaga(action) {
    try {
        const data = yield call(registration, action.payload.data)
        yield put({
            type: REGISTRATION_SUCCESS,
            payload: data
        })
    }
    catch (error) {
        yield call(action.payload.callback)
        yield put({
            type: REGISTRATION_ERROR,
        })
    }
}

function* fetchUserDataSaga(action) {
    try {
        const payload = yield call(fetchUserDataAPI, localStorage.getItem('email'))
        yield put({
            type: FETCH_USER_DATA_SUCCESS,
            payload: payload.data
        })
    }
    catch (error) {
        yield put({
            type: FETCH_USER_DATA_ERROR,
        })
    }
}

export function* authWatcher() {
    yield takeEvery(LOGIN_REQUEST, loginSaga)
    yield takeEvery(REGISTRATION_REQUEST, registrationSaga)
    yield takeEvery(FETCH_USER_DATA_REQUEST, fetchUserDataSaga)
}