import React, { useState, useEffect } from "react";
import { Store } from "react-notifications-component";
import { useNavigate } from "react-router-dom";
import Location from "../../pages/Location";

const LocationsContainer = () => {

    const [locationNumber, setLocationNumber] = useState('')
    const [addMode, setAddMode] = useState(false)
    const [isNumber, setIsNumber] = useState(false)
    const [locationsData, setLocationsData] =  useState([])

    const handleClickAddButton = () => {
        setAddMode(true)
    }

    const handleChangeLocationNumber = (e, data) => {
        if (+data.value < 0) {
            setLocationNumber('0')
        } else
        setLocationNumber(data.value)
    }

    const handleClickAddNumber = () => {
        if (!locationNumber || +locationNumber === 0) {
            console.log('no');
            Store.addNotification({
                message: "The number of locations must be greater than zero",
                type: "warning",
                container: "bottom-right",
                dismiss: {
                    duration: 3000,
                }
            });
        } else {
            let i = 0;
            const array = []
            while (i < +locationNumber) {
                array.push({name: `Location ${i + 1}`, value: ''})
                i++;
            }
            setLocationsData(array)
            setIsNumber(true)
        }
    }

    return (
        <Location
            locationNumber={locationNumber}
            addMode={addMode}
            isNumber={isNumber}
            locationsData={locationsData}
            handleChangeLocationNumber={handleChangeLocationNumber}
            handleClickAddButton={handleClickAddButton}
            handleClickAddNumber={handleClickAddNumber}
        />
    )

}

export default LocationsContainer