import { createStore, combineReducers, applyMiddleware } from "redux";
import { authReducer } from "./auth/reducers";
import { LocationReducer } from "./locations/reducers";
import { videoReducer } from "./userData/reducer";
import { composeWithDevTools } from "redux-devtools-extension";
import createSagaMiddleware from "@redux-saga/core";
import { rootWatcher } from "./sagaWatcher";

const sagaMiddleware = createSagaMiddleware()

const rootReducers =  combineReducers({
    auth: authReducer,
    locations: LocationReducer,
    video: videoReducer

})

export const store = createStore(rootReducers, composeWithDevTools(applyMiddleware(sagaMiddleware)))

sagaMiddleware.run(rootWatcher)